body{
  background: white;
  margin: 0;
  font-family: Roboto,sans-serif;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
}

.App-logo {
  height: 250px;
}

h1{
  color: #E0003C;
 padding-right: 10px;
  margin-right: 10px;
}

