.App-header {
  
  height: 250px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: left;
  font-size: calc(5px + 2vmin);
  color: white;
  border-bottom: 4px solid #E0003C;
}

.App-body {

height: 300px;
text-align: left;
padding: 40px;
}
.App-footer {
  border-top: 4px solid #E0003C;
  height: 100px;
  font-style: italic;
  margin-bottom: 0%;
  }
